section {
    &.mega-z {
      width: 100vw;
      height: 100vh;
      display: flex;
      align-items: center;
      justify-content: center;
      overflow: hidden;
      background: linear-gradient(45deg, #000, #333340) fixed;
      box-shadow: var(--tw-ring-offset-shadow, 0 0 #0000),
        var(--tw-ring-shadow, 0 0 #0000), var(--tw-shadow);
      .wrapper {
        width: 100%;
        height: 100%;
        display: flex;
        align-items: center;
        flex-direction: column;
        padding: 1rem;
        .mega-container {
          position: fixed;
          height: 68% !important;
          width: 100%;
          display: flex;
          flex-direction: column;
          gap: 1rem;
          align-items: center;
          justify-content: flex-start;
          padding-top: 2vw;
          overflow-y: scroll !important;
          -ms-overflow-style: none;
          scrollbar-width: none;
          &::-webkit-scrollbar {
            display: none; /* Safari and Chrome */
          }
          .megas{
            width: 100%;
            max-width: 40rem;
            text-align: center;
            img{
              width: 130px;
            }
            h2{
              font-weight: 500;
              font-size: 1.953rem;
              text-transform: uppercase;
              color: rgba(255, 255, 255, 0.9);
              filter: drop-shadow(0 -1px 0 rgb(0 0 0 / .7)) drop-shadow(0 1px 0 rgb(0 0 0 / .7)) drop-shadow(1px 0 0 rgb(0 0 0 / .7)) drop-shadow(-1px 0 0 rgb(0 0 0 / .7));
  text-shadow: 0 1px 0 rgba(0,0,0,.7), 1px 0 0 rgba(0,0,0,.4), -1px 0 0 rgba(0,0,0,.4);
          }
          }
          .grid{
              display: grid;
              grid-template-columns: repeat(3, 1fr);
              gap: 7px;
              .box{
                  border: 1px solid #222;
                  height: 137px;
                  img{
                      width: 115px;
                      height: 100%;
                      &:hover{
                          filter: contrast(130%);
                      }
                  }
              }
          }
        }
      }
    }
  }
  