section{
    &.collections{
        width: 100vw;
        height: 100vh;
        display: flex;
        align-items: center;
        justify-content: center;
        overflow: hidden;
        background: linear-gradient(45deg, #000, #333340) fixed;
        box-shadow: var(--tw-ring-offset-shadow, 0 0 #0000),
    var(--tw-ring-shadow, 0 0 #0000), var(--tw-shadow);
        .wrapper {
          width: 100%;
          height: 100%;
          display: flex;
          align-items: center;
          flex-direction: column;
          padding: 1rem;
    }
}
}