section {
  &.menu {
    width: 100vw;
    height: 100vh;
    display: flex;
    align-items: center;
    justify-content: center;
    overflow: hidden;
    background: linear-gradient(45deg, #000, #333340) fixed;
    box-shadow: var(--tw-ring-offset-shadow, 0 0 #0000),
      var(--tw-ring-shadow, 0 0 #0000), var(--tw-shadow);  
    .wrapper {
      width: 100%;
      height: 100%;
      display: flex;
      align-items: center;
      flex-direction: column;
      padding: 1rem;
      .menu-content{
        position: absolute;
        height: 86vh;
        display: flex;
        align-items: center;
        justify-content: center;
      
      
        .menus{
          margin-bottom: 4rem;
          display: flex;
          flex-direction: column;
          text-align: center;
          gap:2vh;
          
          span{
           
            z-index: 0;
            font-size: 1.563rem;
            text-transform: uppercase;
            letter-spacing: .1em;
            color: rgba(255, 255, 255, 0.9);
            line-height: 40px;
            text-shadow: -1px 1px #000, 0 -1px #000, 1px 1px #000, 1px 0 #000, 1px 2px #000, 0 1px #000, -1px 1px #000, -1px 0 #000;
            transform: scale(1, 1.2);
            &:hover{
              filter: drop-shadow(3px 3px 4px rgba(243, 183, 1, 0.5)) drop-shadow(-3px -3px 2px rgba(230, 116, 2, 0.5));
              transform: scale(1, 1.5);
              color: #050035;
              text-shadow: -1px 1px #fff, 0 -1px #fff, 1px 1px #fff, 1px 0 #fff, 1px 2px #fff, 0 1px #fff, -1px 1px #fff, -1px 0 #000;
              opacity: 0.8;
            }
            &:focus{
              animation: crtTextShadow 1s infinite
            }
            &:active {
              animation: crtTextShadow 1s infinite
            }
          }
        }
        
      }
      
    }
  }
}
